
.cell-title {
  font-size: 0.8em;
}
.cell-title .reply_count {
  color: #9e78c0;
}
.cell-title .visit_count {
  color: #b4b4b4;
}
.cell-title .top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  color: #fff;
}
.cell-title .tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  font-size: 12px;
}
.author_avatar img{
    width: 30px;
    height: 30px;
    border-radius: 3px;
    margin-left: 5px;
}



